* {
  box-sizing: border-box;
  font-family: "Arial";
}
:root {
  /* COLORS */
  --dark: #071739;
  --between: #0b2255;
  --medium: #5e6472;
  --light: #dcdfe6;
  font-size: 16px;
}
html,
body {
  background-color: var(--dark);
  min-height: 100%;
  max-height: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
/* ANIMATION CLASSES */
@keyframes appearFromTop {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }
  to {
    transform: translateY(0%);
    opacity: 1;
  }
}

.button {
  margin: 5vh 0;

  width: 70vw;
  min-width: 300px;

  height: 10vh;
  min-height: 70px;

  border: 0.5rem solid var(--medium);
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: var(--medium);
  transition: transform 200ms, background-color 200ms, color 200ms;
  animation: 1s ease-out appearFromTop 1;
}
.button:hover {
  transform: scale(1.05) ;
  color: var(--light);
  background-color: var(--between);
}
.button:focus {
  outline: none;
}
a {
  text-decoration: none;
}
